import { Field, ObjectType } from "@nestjs/graphql";
import { Column, Entity, OneToMany } from "typeorm";
import { BaseEntityColumn } from "../base.entity";
import { PaymentGatewayKey } from "./payment-gateway-key.entity";
import { PaymentGatewayPlan } from "./payment-gateway-plan.entity";

@ObjectType()
@Entity()
export class PaymentGateway extends BaseEntityColumn {

  @Field(() => String)
  @Column()
  name: string;

  @Field(() => String)
  @Column()
  apiSandboxTokenUrl: string;

  @Field(() => String)
  @Column()
  apiSandboxPaymentUrl: string;

  @Field(() => String)
  @Column()
  apiTokenUrl: string;

  @Field(() => String)
  @Column()
  apiPaymentUrl: string;

  @Field(() => [PaymentGatewayKey], { nullable: true })
  @OneToMany(() => PaymentGatewayKey, (paymentGatewayKey) => paymentGatewayKey.paymentGateway)
  paymentGatewayKeys: PaymentGatewayKey[];

  @Field(() => [PaymentGatewayPlan], { nullable: true })
  @OneToMany(() => PaymentGatewayPlan, (paymentGatewayPlan) => paymentGatewayPlan.paymentGateway, { cascade: true })
  paymentGatewayPlans: PaymentGatewayPlan[];

}
